import { previewFactory, listenPanelExpanded, getSidebarCustomize } from "@devowl-wp/customize";
import { RootStore } from "../store";

/**
 * Apply common callbacks in customize banner preview.
 */
function applyCustomizeBannerPreview() {
  const {
    customizeBannerStore,
    optionStore,
    checklistStore
  } = RootStore.get;
  const {
    others: {
      customizeIdsBanner: {
        panel,
        sections,
        settings
      }
    }
  } = optionStore;

  /**
   * We need to debounce all our bound events. Why? When you do a
   * page reload or navigation, the reference to the e. g. store is lost
   * and Chrome is stuck.
   */
  const debounce = callback => setTimeout(callback, 0);

  // When hovering over "checkbox checked state" controls, simulate all checkboxes as checked
  const customize = getSidebarCustomize();
  const {
    group
  } = settings;
  [group.checkboxActiveBg, group.checkboxActiveBorderColor, group.checkboxActiveColor].forEach(id => customize.control(id, _ref => {
    let {
      container
    } = _ref;
    return container.hover(() => debounce(() => customizeBannerStore.setPreviewCheckboxActiveState(true)), () => debounce(() => customizeBannerStore.setPreviewCheckboxActiveState(false)));
  }));

  // Automatically open the individual privacy settings when opening the given sections
  [sections.individualLayout, sections.group, sections.saveButton, sections.individualTexts].forEach(sectionId => listenPanelExpanded(sectionId, state => {
    debounce(() => customizeBannerStore.setIndividualPrivacyOpen(state));
  }));
  previewFactory({
    immediateApply: {
      texts: settings.texts,
      individualTexts: settings.individualTexts,
      legal: {
        imprintLabel: settings.legal.imprintLabel,
        privacyPolicyLabel: settings.legal.privacyPolicyLabel
      }
    },
    onChange: [settings, (section, setting, newValue) => debounce(() => customizeBannerStore.setBannerFromCustomize(section, setting, newValue))],
    onPanelChange: [panel, state => debounce(() => {
      customizeBannerStore.setVisible(state);
      state && checklistStore.toggleChecklistItem("customize-banner", true);
    })]
  });
}
export { applyCustomizeBannerPreview };